import React, { useEffect, useState } from 'react';
import Reveal from 'react-awesome-reveal';
import { keyframes } from "@emotion/react";

import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
import nftAbi from '../abis/nft.json';

const fadeInUp = keyframes`
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
`;



const useSlidermainzero = function() {
  const [account, setAccount] = React.useState("");
  const [showContent, setShowContent] = React.useState("");

  const mintAirDrop = async() => {
   if(typeof window.ethereum!=='undefined'){

      const web3 = new Web3(window.ethereum);
      const accounts = await web3.eth.getAccounts();
      const account = accounts[0];
      Web3EthContract.setProvider(web3);

      const SmartContractObj = new Web3EthContract( nftAbi, "0x6c31916d634BBc7Ec2fc8A264789d3d7832D4497" );
      const receipt = await SmartContractObj.methods.mintAirDrop(account).send({from: account});
      console.log(receipt);
      checkAirdrop();
  }
}

  const checkAirdrop = async() => {

    if(typeof window.ethereum!=='undefined'){
      const web3 = new Web3(window.ethereum);
      const accounts = await web3.eth.getAccounts();
      const account = accounts[0];
      setAccount(account);

      if(typeof account !=='undefined'){
        Web3EthContract.setProvider(web3);

        const SmartContractObj = new Web3EthContract( nftAbi, "0x6c31916d634BBc7Ec2fc8A264789d3d7832D4497" );

        const receipt = await SmartContractObj.methods.airdrop(account).call();

        let content = [];

        if (receipt.receiveAirdrop) {
          content.push(<>
            <div className="col-md-12 align-items-center text-center">
                    <span>
                        <img className="lazy" src="/img/airdrop/airdrop.png" alt="" width="300px" />
                    </span>
                </div>
                <div className="spacer-double"></div>
                <Reveal className='onStep' keyframes={fadeInUp} delay={300} duration={600}>
                <h1 className="text-center">Airdrop NFT</h1>
                </Reveal>
                <Reveal className='onStep' keyframes={fadeInUp} delay={600} duration={600}>
                <p className="lead text-center">
                congratulations
                </p>
                </Reveal>
                <div className="spacer-10"></div>
                <Reveal className='onStep' keyframes={fadeInUp} delay={800} duration={600}>
                <div className='d-flex justify-content-center'>
                  <span onClick={()=> mintAirDrop()} className="btn-main inline lead">Mint Airdrop</span>
                </div>
                <div className="spacer-single"></div>
                <div className="spacer-double"></div>
                </Reveal></>);
        }else{
          content.push(<>
                <div className="spacer-double"></div>
                <Reveal className='onStep' keyframes={fadeInUp} delay={300} duration={600}>
                  <h1 className="text-center">Sorry</h1>
                </Reveal>
                
                <div className="spacer-single"></div>
                <div className="spacer-double"></div>
                </>);
        }

        setShowContent(content);
      }
      
    }

  }
//
  useEffect(() => {
    checkAirdrop();

  }, []);

  return(
    <div className="container">
      <div className="row align-items-center">
            <div className="col-md-6 m-auto">
                <div className="spacer-single"></div>

                {showContent}

                
            </div>
        </div>
    </div>
  );
};
export default useSlidermainzero;