import React from 'react';
import Reveal from 'react-awesome-reveal';
import { keyframes } from "@emotion/react";
import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";

import nftAbi from '../abis/nft.json';

const fadeInUp = keyframes`
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
`;
const inline = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
  .d-inline{
    display: inline-block;
   }
`;

const mysteryBox = async() => {
   if(typeof window.ethereum!=='undefined'){

      const web3 = new Web3(window.ethereum);
      const accounts = await web3.eth.getAccounts();
      const account = accounts[0];

      Web3EthContract.setProvider(web3);

      const SmartContractObj = new Web3EthContract( nftAbi, "0x6c31916d634BBc7Ec2fc8A264789d3d7832D4497" );

      //var nft = new web3.eth.Contract(nftAbi.abi, "0x6c31916d634BBc7Ec2fc8A264789d3d7832D4497");

      let _amount = 0.001 * 10**18;
      const receipt = await SmartContractObj.methods.mint(1).send({value: _amount.toString(),from: account});
      console.log(receipt);
  }
}

const slidermainparticle= () => (
 <div className="container">
    <div className="row align-items-center">
          <div className="col-md-6">
              <div className="spacer-single"></div>
              
              <Reveal className='onStep' keyframes={fadeInUp} delay={300} duration={900} triggerOnce>
              <h1 className="col-white">Discover <span className="color">rare</span> digital art and collect NFTs</h1>
              </Reveal>

              <Reveal className='onStep' keyframes={fadeInUp} delay={600} duration={900} triggerOnce>
              <p className="lead col-white">
              Unit of data stored on a digital ledger, called a blockchain, that certifies a digital asset to be unique and therefore not interchangeable
              </p>
              </Reveal>
              <div className="spacer-10"></div>
              <Reveal className='onStep d-inline' keyframes={inline} delay={800} duration={900} triggerOnce>

              <span onClick={()=> mysteryBox()} className="btn-main inline lead">MINT</span>
              <span onClick={()=> window.open("https://testnets.opensea.io/collection/boott")} className="btn-main inline lead white">Explore Collection</span>

              <div className="mb-sm-30"></div>
              </Reveal>


          </div>
      </div>
    </div>
);

export default slidermainparticle;